import React from 'react'
import Header from '../../../common/heading/Header'
import Footer from '../../../footer/Footer'
import './Electricit.css'
import { Link } from 'react-router-dom'
function Electricit() {
  return (
    <div className='electricitTop_Components'>
      <Header />
      <div className='electricitTop'>
        <h1><em>Electricit</em></h1>
        <p>O se situe le problme ?</p>
        <hr />
        <div className='electricitSecond'>
          <Link className='electricitlinkTop' to={'/electricitIncre'}>Tableau lectrique</Link> <br />
          <Link className='electricitlinkTop' to={'/electricitIncre2'}>Prises,interrupteurs ou prises Internet</Link>
        </div>
      </div>
      <Footer />
    </div>
  )
}

export default Electricit