import React from 'react';
import './services.css'
import { Link } from 'react-router-dom';
import Header from '../../components/common/heading/Header';
import Footer from '../../components/footer/Footer';
function Services() {
  return (
     <div className='servicesTopPage'>
      <Header/>
    <div className='servicesTop'>
       
      <h1>Tous les services</h1>
      <div className='servicesDesplay'>
        <img className='imageS'  src="Img4.png" alt="" />
        <img className='iconsArrow'  src="icons8-arrow-64.png" alt="" />
        <div className='servicesPartie'>
        <Link to={'/depannage'}><h4>Dpannage et rparation</h4></Link>
        <p>Un problme de plomberie, d'lectricit, de serrurerie ? Une fuite deau, un chauffe-eau  remplacer... Pour toutes les prestations de dpannage ...</p>
        </div>
      </div>
      <div className='servicesDesplay'>
        <img className='imageS'  src="Img2.png" alt="" />
        <img className='iconsArrow'  src="icons8-arrow-64.png" alt="" />
      <div className='servicesPartie'>
        <Link to={'/petits_travaux'}><h4>Petits travaux</h4></Link>
        <p>Des envies de bricolage ou des petits travaux dembellissement de vos espaces intrieurs et extrieurs (peinture, pose de carrelage, nouvelle installation lectrique, parquet ou stores vnitiens ...)</p>
      </div>
      </div>
    </div>
    <Footer/>
    </div>
  );
}

export default Services;
