import React, { useState } from 'react';
import './accordion.css'
function Accordion() {
    const [selected, setSelected] = useState(null)
    const toggle = (i) => {
        if (selected === i) {
            setSelected(null);
        } else {
            setSelected(i);
        }
    };
    const data = [
        {
            question: "Pourquoi passer par Bin_Edk ?",
            answer: "Vous bnficiez d'un interlocuteur unique, d'artisans sourcs et forms par nos partenaires, et une transparence des prix tant que la main d'uvre que la fourniture. En passant par Saweblia, vous navez pas de mauvaises surprises et si vous n'tes pas satisfait, nous serons  vos cts pour trouver une solution jusqu' lexcution finale des travaux."
        },
        {
            question: "Comment fonctionne votre service ?",
            answer: "Vous pouvez nous contacter par tlphone ou par WhatsApp sur le +212 677 330 404.Lorsque nous recevons votre demande, un charg de relation client prend en charge votre demande et vous aide  dfinir votre besoin.Nous pouvons tre amens  organiser une visite avec lun de nos coordinateurs de travaux pour les demandes complexes. Lorsque le besoin est clair, nous vous envoyons un devis dans un dlai allant de 20 minutes  48 heures (dpendamment de la nature des travaux  raliser). Ds que vous confirmez le devis, nous programmons une intervention selon vos disponibilits."
        },
        {
            question: "Est-ce que vous avez une application ?",
            answer: "Pour l'instant, nous recevons vos demandes via le site Internet, WhatsApp, nos rseaux sociaux ou par tlphone.Nous travaillons sur une nouvelle application qui sera bientt disponible !"
        },
        {
            question: "Comment et  quel moment dois-je payer ma prestation?",
            answer: "Vous pouvez payer en ligne par carte bancaire sur le site Internet, par chque, virement bancaire ou en espces.Si le montant de votre commande dpasse 1000 Dhs, un acompte vous sera demand pour prparer l'intervention. "
        },
        {
            question: "Comment slectionnez-vous vos artisans ?",
            answer: "Tous nos artisans ont un statut lgal et leur identit ainsi que leur casier judiciaire sont vrifis par nos quipes.Nos artisans suivent un processus en plusieurs tapes pour nous assurer de leur expertise mtier et leur savoir-tre. Ils passent plusieurs niveaux de validation avant d'arriver chez vous :)Lorsquils sont valids, nous les formons  notre charte qualit interne et faisons un suivi permanent pour nous assurer dune qualit de service continue.Enfin, nous accompagnons nos artisans avec des incentives sociales (CIMR, assurance maladie,), des formations mtiers avec nos partenaires (Lydec, Weber, Hilti) et un accs au financement avec notre partenaire CIH."
        },
        {
            question: "Vos fournitures et matriaux sont-ils de bonne qualit ?",
            answer: "Tous nos produits sont garantis par nos fournisseurs que nous slectionnons sur plusieurs niveaux.Si le moindre problme apparat pendant la dure de garantie, nous nous engageons  remplacer ou  rparer le matriel selon les conditions de garantie du constructeur. "
        }
    ];

    return (
        <div className="wrapper" id='accordion'>
            <div className="accordion">
            <h1 className='h1Title'>Foire aux questions</h1>
                {data.map((item, i) => (
                    <div className="item" key={i}>
                        <div className="title" onClick={() => toggle(i)}>
                            <h3>{item.question}</h3>
                            <span style={{fontSize:'25px'}}>{selected === i ? "-" : "+"}</span>
                        </div>
                        <div className={selected === i ? "content show" : "content"}>
                            {item.answer}
                        </div>
                    </div>
                ))}
            </div>
            
        </div>
    );
}

export default Accordion;
